                    <p className='describe'>Kathakali (Malayalam: ) is a major form of classical Indian dance.[1] It is a "story play" genre of art, but one distinguished by the elaborately colourful make-up, costumes and face masks that the traditionally male actor-dancers wear.[2][3][note 1] Kathakali is a performing art in the Malayalam-speaking southwestern region of Kerala.[2][3][5]


Kathakali is one of the eight classical dances of India

Hanuman in Kathakali (FACT Jayadeva Varma)
Kathakal's roots are unclear. The fully developed style of Kathakal originated around the 17th century, but its roots are in the temple and folk arts (such as Krishnanattam and religious drama of the kingdom of the Zamorin of Calicut) southwestern Indian peninsula), which are traceable to at least the 1st millennium CE.[2][6] A Kathakali performance, like all classical dance arts of India, synthesizes music, vocal performers, choreography and hand and facial gestures together to express ideas. However, Kathakali differs in that it also incorporates movements from ancient Indian martial arts and athletic traditions of South India.[2][3][5] Kathakal also differs in that the structure and details of its art form developed in the courts and theatres of Hindu principalities, unlike other classical Indian dances which primarily developed in Hindu temples and monastic schools.[2][6]

The traditional themes of the Kathakal are folk stories, religious legends and spiritual ideas from the Hindu epics and the Puranas.[7] The vocal performance has traditionally been performed in Sanskritised Malayalam.[6] In modern compositions, Indian Kathakali troupes have included women artistes,[4] and adapted Western stories and plays such as those by Shakespeare.[8]</p>
